import React from 'react';
import './Details.css';
import '../images/emp-name.jpg';
import Modal from '../Modal/Modal';

class Details extends React.Component {
	constructor(props) {
		super(props);
	
		this.state = { isOpen: false };
	  }
	
	  toggleModal = () => {
		this.setState({
		  isOpen: !this.state.isOpen
		});
	  }

	render(){
		return (
			<div className="container">
				<Modal	show={this.state.isOpen}
		  				onClose={this.toggleModal}>
					
		   		</Modal>
				<section>
					<table>
						<thead>
							<tr>
								<th>Employee id</th>
								<th>Neme</th>
								<th>Salery</th>
								<th>Age</th>
								<th>Photo</th>
								<th>Action</th>
							</tr>
						</thead>
						<tbody>
							<tr>
								<td>1</td>
								<td>Rahul</td>
							
								<td>60,000.00</td>
								<td>35 years</td>
								<td><img src={require('../images/emp-name.jpg')} alt="emp-name.jpg" /></td>
								<td>
									<button className='btn-green' onClick={this.toggleModal}>Edit</button>
									<button className='btn-red'>Delete</button>
								</td>
							</tr>
							<tr>
								<td>2</td>
								<td>Raju</td>
							
								<td>50,000.00</td>
								<td>30 years</td>
								<td><img src={require('../images/emp-name.jpg')} alt="emp-name.jpg" /></td>
								<td>
									<button className='btn-green' onClick={this.toggleModal}>Edit</button>
									<button className='btn-red'>Delete</button>
								</td>
							</tr>
						</tbody>
					</table>
					
				</section>
				
			</div>
		)
	}
}

export default Details;