import React from 'react';
import './Form.css';

class Form extends React.Component {

	constructor(props){
		super(props);
		this.state = {  Editemp_id:"1",
						EditName: "Rahul",
						EditSalery:"60000.00",
						EditAge:"35",
						EditPhoto:"Photo"
		}
	}

	handlechangeEdit = (event) => {
		this.setState({ [event.target.name] :event.target.value })
	}

	handleEdit = (event) => {
		alert( JSON.stringify(this.state));
		event.preventDefault();
	}

render(){
	return(
		<div id="form">
			<h3> Edit an Employee </h3>
			<form onSubmit = { this.handleEdit } >
				<label className="label"> Id </label>
				<input type="number" name="Editemp_id" value={this.state.Editemp_id}
					onChange={this.handlechangeEdit} />

                <label className="label"> Name </label>
				<input type="text" name="EditName" value={this.state.EditName}
					onChange={this.handlechangeEdit} />
				
				<label className="label"> Salery </label>
				<input type="number" name="EditSalery" value={this.state.EditSalery}
					onChange={this.handlechangeEdit} />
				
				<label className="label"> Age </label>
				<input type="number" name="EditAge" value={this.state.EditAge}
					onChange={this.handlechangeEdit} />
				
				<label className="label"> Photo </label>
				<input type="button" name="EditPhoto" id="photo" placeholder={this.props.Photo} value={this.state.EditPhoto}
					onChange={this.handlechangeEdit} />
				<input type="file" id="my_file" style={{display: 'none'}} />

				<input type="submit"  value="Save" />

			</form>
		</div>
	)
}

}

export default Form;